function transformText(node,context){
  if(node.type===NodeTypes.TEXT||node.type===NodeTypes.INTERPOLATION){
  console.log('文本进入',node,context)

    return ()=>{

      console.log('文本推出')
    
  }
  }
}

function createTransformContext(root){
  const context={
    currentNode:root,
    parent:null,

    helpers:new Map(),//用于存储用的方法
    helper(name){
      const count=context.helpers.get(name)||0
      context.helpers.set(name,count+1);
      return name

    },
    removeHelper(name){
      const count=context.helpers.get(name)
      const currentCount=count-1
      if(!currentCount){
        context.helpers.delete(name)
      }else{
        context.helpers.set(name,currentCount)
      }

    },
    nodeTransform:[
      transformExpression,
      transformElement,
      transformText,
    ]
  }

  return context

}

// vue2 中转化只做了标记  vue3中的patchFlags block处理
function traverseNode(node,context){
  console.log(node,context,'traverseNode')
  context.currentNode=node

  const transforms=context.nodeTransform;//获取所有的转化方法
  let exitFns=[]
  for(let i=0;i<transforms.length;i++){
    let exitFn=transforms[i](node,context)
    exitFn&&(exitFns.push(exitFn))

  }

  switch(node.type){
    case NodeTypes.ROOT:
    case NodeTypes.ELEMENT:
      for(let i=0;i<node.children.length;i++){
        context.parent=node
        traverseNode(node.children[i],context)
      }

    case NodeTypes.INTERPOLATION:
      context.helper(TO_DISPLAY_STRING);
      break
  }

  // 整个儿子执行完毕后  依次调用再推出函数
  let len=exitFns.length
  context.currentNode=node
  while(len--){
    exitFns[len]();
  }

}

function transform(root){
  // 可以将一直使用的东西  通过上下文存储  co的源码  遍历树
  const context=createTransformContext(root)

  // dom树遍历 只能采用深度遍历
  traverseNode(root,context)

}

export function compile(template){
  const ast= parser(template)

  console.log(ast,'ast')

  transform(ast);//对ast语法树进行转化   给ast节点是增加一些额外的信息  codegenNode 收集需要导入的方法

  return

}